#bgprocesos {
  height: 2400px;
  opacity: 20%;
  width: 100%;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  margin: 0 auto;
}
.inner__container {
  position: absolute;
  top: 0;
  left: 27%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inner__container h3 {
  margin-top: 50px;
  font-size: 25pt;
  margin-bottom: 40px;
}
.imgprocesos {
  height: 300px;
  max-width: 350px;
  overflow: hidden;
}
.im1 {
  margin-right: 100px;
}
.im2 {
  margin-left: 100px;
}
.tex2 {
  text-align: right;
}
.procesos__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 50px;
}
.proc__text {
  width: 500px;
}
.proc__text h4 {
  font-size: 17pt;
  margin-bottom: 15px;
}
.proc__text p {
  line-height: 1.5;
  font-size: 12pt;
}
.ic1 {
  height: 130px;
  width: 130px;
  border-radius: 50%;
  background-color: var(--color-bg);
  display: flex;
  align-items: center;
  box-shadow: 2px 2px 5px var(--color-textgrey);
  position: absolute;
  left: 270px;
}
.ic2 {
  height: 130px;
  width: 130px;
  border-radius: 50%;
  background-color: var(--color-bg);
  display: flex;
  align-items: center;
  box-shadow: 2px 2px 5px var(--color-textgrey);
  position: absolute;
  right: 270px;
}

.proc__icons {
  height: 70px;
  margin: 0 auto;
}
.proc__icons:hover {
  -webkit-animation: rotate-center 0.6s ease-in-out both;
  animation: rotate-center 0.6s ease-in-out both;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  #bgprocesos {
    height: 3050px;
  }
  .inner__container {
    top: 0;
    left: auto;
    width: 100%;
  }
  .inner__container h3 {
    width: 600px;
    text-align: center;
  }
  .procesos__container {
    flex-direction: column;
    margin-top: 50px;
    gap: 15px;
  }
  .im1 {
    margin-right: 0px;
  }
  .im2 {
    display: none;
  }
  .ic1 {
    display: none;
  }
  .ic2 {
  left: 25px;
  }
  .proc__text{
    text-align: center;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  .inner__container h3 {
    width: 370px;
    text-align: center;
  }
  .proc__text{
   width: 350px;
  }
  .ic2 {
    display: none;
    }
    #bgprocesos {
      height: 3500px;
    }
}
