#image1{
    height: 325px;
    max-width: 325px;
    overflow: hidden;
    display: inline-block;
    margin-left: 120px;
}


#image2{
    height: 170px;
    max-width: 190px;
    overflow: hidden;
    position: relative;
    top: -270px;
    left: -10px;
    border-style: solid;
    border-color: var(--color-bg);
    border-width: 10px;
}

#gestion_container{
    margin-top: 80px;
    position: absolute;
    top: 0;
    left: 25%;

}
#divider1{
    max-height: 3px;
    max-width: 60px;
    margin-bottom: 20px;
    margin-top: 10px;
}
h3{
    font-size: 20pt;
    color: var(--color-midgreen);
    margin-bottom: 20px;
}
.gestiontext{
    max-width: 500px;
    display: flex;
    flex-direction: column;
    position: relative;
    top: -290px;
    right: -500px;
}
#textblock{
    margin-top: 15px;
    font-size: 11pt;
    text-align: justify;
}

.gestiondesoca{
    height: 550px;
}

#bggestion{
    transform: scaleX(-1);
    opacity: 20%;
    width: 100% ;
    height: 600px;
    margin-bottom: 50px;
}

/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
    #bggestion{
       height: 600px;
    }
    .gestiondesoca{
        height: 500px;
    }
    #image1{
        height: 200px;
        max-width: 225px;
        overflow: hidden;
        display: inline-block;
        margin-left: 35%;
    }
    #gestion_container{
        margin-top: 70px;
        position: absolute;
        top: 0;
        left: 15%
    
    }
    #image2{
        height: 120px;
        max-width: 140px;
        top: -280px;
        left: 100px;
    }
    .gestiontext{
        max-width: 500px;
        top: 90px;
        right: -50px;
        align-items: center;
    }
    #gestion_container{
        margin-top: 40px;
    
    }
  }
  /* Media Queries (Small Devices) */
  
  @media screen and (max-width: 600px) {
    #bggestion{
        height: 700px;
     }
     .gestiondesoca{
         height: 500px;
     }
     #image1{
         height: 200px;
         max-width: 225px;
         overflow: hidden;
         display: inline-block;
         margin-left: 5%;
     }
     #gestion_container{
         margin-top: 70px;
         position: absolute;
         top: 0;
         left: 15%
     
     }
     #image2{
         height: 120px;
         max-width: 140px;
         top: -340px;
         left: -30px;
     }
     #gestion_container{
        margin-top: 40px;
    
    }
    .gestiontext{
        max-width: 500px;
        top: 70px;
        right: 25px;
        align-items: center;
    }
    .gestiontext h3{
        margin-bottom: 5px;
    }
    #divider1{
        margin-bottom: 5px;
    }
  }
  