.footercont {
  padding: 30px;
  background-color: var(--color-midgreen);
}
.threecont {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 250px;
  margin-left: 15%;
}
#socalogoft {
  width: 200px;
}
.foot2_cont a,
.foot2_cont {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  color: var(--color-lightext);
}
.icons1 {
  font-size: 30pt;
}

.conta {
  font-family: FuturaBd;
  margin-bottom: 20px;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  .threecont {
    gap: 45px;
    margin-left: 15px;
  }
  .conta {
    margin-left: 50px;
  }
  .copyright1{
    font-size: 25pt;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  .threecont {
    flex-direction: column;
    align-items: center;
    gap: 50px;
    margin-left: 15px;
  }
  .foot2_cont a,
.foot2_cont {
text-align: center;
}
.copyright1{
  font-size: 22pt;
  margin-bottom: 30px;
}
}
