header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#leaves {
  height: auto;
  max-width: 100%;
  filter: brightness(70%);
  position: relative;
}
.header__container {
  margin: 0 auto;
  width: auto;
}
.logoandtext {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 0;
}
.contsmall {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 60px;
}
.contsmall a {
  cursor: pointer;
}
#walogo {
  height: 50px;
  display: flex;
}
#softwarebutton {
  height: 45px;
  display: flex;
  align-items: center;
}
#menustripe {
  height: 50px;
  width: 57%;
  background-color: var(--color-midblue);
  position: absolute;
  top: 160px;
  display: flex;
  align-items: center;
  padding-left: 20px;
}
#menu_logostripe {
  height: 100px;
  width: 57%;
  background-color: var(--color-bg);
  margin: 0 auto;
  position: absolute;
  top: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

#headertext1 {
  text-align: left;
  position: absolute;
  top: 8px;
  left: 415px;
}
#softwarebutton {
  margin-left: 50px;
}

#socalogo {
  height: 80px;
  margin-left: 10px;
}

#hshlogo {
  height: 100px;
}
#homelink:hover {
  color: var(--color-toxicgreen);
}

#floatingtext_container {
  color: var(--color-lightext);
  position: absolute;
  top: 300px;
  left: 13%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 40pt;
  line-height: 1.2;
}

#conocebtn {
  margin-top: 50px;
  padding-left: 40px;
  padding-right: 40px;
  font-size: 15pt;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  .encase {
    background-color: var(--color-midgreen);
    padding-top: 160px;
  }
  #socalogo {
    height: 50px;
  }
  .logoandtext {
    display: none;
  }
  #conocebtn {
    margin-top: 40px;
    font-size: 15pt;
  }
  .image-layer {
    height: 500px;
    background-size: 100%;
  }
  #headertext1 {
    text-align: left;
    position: absolute;
    top: 8px;
    left: 25%;
  }

  #headertext2 {
    text-align: right;
    position: absolute;
    top: 25px;
    right: 30%;
  }
  .contsmall {
    display: none;
  }
  #floatingtext_container {
    top: 300px;
  }
  h1 {
    font-size: 25pt;
    line-height: 1.2;
  }
  #leaves {
    height: 500px;
    max-width: 100%;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  #menu_logostripe {
    height: 150px;
    width: 75%;
    border-radius: 15px;
    top: 60px;
    right:45px;
  }

  #headertext2 {
    display: none;
  }
  #headertext1 {
    line-height: 1;
  }
  #softwarebutton {
    display: none;
  }
  #socalogo {
    height: 75px;
  }
  #menustripe{
    display: none;
  }
  #floatingtext_container {
    top: 300px;
  }
  h1 {
    font-size: 13pt;
    line-height: 1.7;
  }
  #leaves {
    height: 600px;
    max-width: 100%;
  }
}
