.getinfo {
  background-color: var(--color-midblue);
}

.getinfo h3 {
  width: 450px;
  line-height: 1;
  font-size: 25pt;
}

.getinfo input {
  background-color: var(--color-bg);
  color: var(--color-textgrey);
  border-style: solid;
  border-color: var(--color-bg);
  border-width: 1px;
  height: 40px;
  width: 300px;
  font-size: 13pt;
  font-family: Aceh;
  padding: 0.5rem 0.8rem 0.5rem 0.8rem;
}
.getinfo button {
  margin-left: 20px;
  font-size: 12pt;
  font-family: Aceh;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-style: solid;
  border-color: var(--color-toxicgreen);
  border-width: 1px;
}
.getinfo button:hover {
  border-style: solid;
  border-color: var(--color-bg);
  border-width: 1px;
  background-color: var(--color-textgrey);
}
.arrowrighticon {
  margin-left: 10px;
}
.container__getinfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  height: 275px;
  width: fit-content;
}

.textimputinfo {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.inputs{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  .getinfo h3 {
    width: 450px;
    line-height: 1;
    font-size: 18pt;
  }
  .inputs{
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-left: 20px;
  }
  .getinfo input {
    height: 40px;
    width: 250px;
  }
  .getinfo button {
    margin-left: 20px;
    font-size: 14pt;
    font-family: Aceh;
    height: auto;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  .container__getinfo {
    flex-direction: column;
    gap: 10px;
  }
  .getinfo{
    height: 400px;
  }
  .getinfo h3 {
    margin-top: 20px;
    width: 350px;
    line-height: 1;
    font-size: 18pt;
    text-align: center;
  }
  .inputs{
    gap: 10px;
    margin-left: 0;
  }
}
