.goTopHidden{
    display: none;
}
.goTopShow{
    position: fixed;
    z-index: 2;
    left: 90%;
    top: 90%;
}
.goTop{
    width: 55px;
    height: 55px;
    box-shadow: 2px 2px 5px var(--color-textgrey);
}
.arrowupicon{
    font-size: 20pt;
    display: flex;
    margin-left: -1px;

}

/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
    .goTopShow{
        position: fixed;
        z-index: 2;
        left: 80%;
        top: 85%;
    }
    .goTop{
        width: 50px;
        height: 50px;
    }
    .arrowupicon{
        font-size: 15pt;
    
    }
  }