#bgsolic {
  max-height: 900px;
  width: 100%;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  margin: 0 auto;
  filter: brightness(50%);
}

#conten__solic {
  margin-top: 80px;
  position: absolute;
  top: 0;
  left: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.solic__text {
  width: 800px;
  text-align: center;
  line-height: 1;
}

.solic__text h3 {
  font-size: 33pt;
  margin-bottom: 35px;
}

.solic__text h4 {
  font-family: FuturaMd;
  font-size: 28pt;
  margin-bottom: 35px;
}

.container__solicitud {
  width: var(--container-width-lg);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: var(--color-bg);
  padding: 40px;
}
.container__solicitud input {
  font-family: Aceh;
  font-size: 12pt;
  border-style: solid;
  border-color: var(--color-lightgrey);
  border-width: 1px;
  height: 45px;
  padding-left: 10px;
  margin-bottom: 25px;
}
.cont__input {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}
.cont__input input {
  width: 300px;
}
.whatsappnumb {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ayudarap {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ayudarap h5 {
  font-size: 8pt;
  font-family: FuturaMd;
  margin-bottom: 20px;
}
.ayudarap p {
  font-family: FuturaBd;
  margin-bottom: 20px;
}
.ayudarap a {
  color: var(--color-midgreen);
  display: flex;
  flex-direction: row;
  align-items: center;
}
.asuntodiv input {
  width: 217px;
  margin-right: 20px;
  flex: 1;
}
.asuntodiv button {
  font-size: 12pt;
  font-family: Aceh;
  height: 50px;
  width: 200px;
  flex: 1;
}

/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  #bgsolic {
    width: 100%;
    height: 600px;
    filter: brightness(50%);
  }
  #conten__solic {
    margin-top: 50px;
    top: 0;
    left: 0;
    margin-left: 0px;
    width: 100%;
  }
  #solicitud {
    height: 600px;
  }
  .solic__text {
    width: 600px;
  }
  .solic__text h3 {
    font-size: 25pt;
    width: 600px;
    margin-bottom: 20px;
  }
  .solic__text h4 {
    width: 600px;
    font-family: FuturaMd;
    font-size: 18pt;
    margin-bottom: 20px;
  }
  .container__solicitud {
    padding: 20px;
    margin-left: 0;
    margin-right: 0;
    width: 650px;
  }
  .cont__input {
    gap: 10px;
  }
  
  .cont__input input {
   width: 200px;
  }
  .asuntodiv input {
    margin-right: 10px;
    width: 200px;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  #bgsolic {
    width: 100%;
    height: 890px;
    filter: brightness(50%);
  }
  #solicitud {
    height: 890px;
  }
  .solic__text {
    width: 360px;
  }
  .solic__text h3 {
    font-size: 21pt;
    width: 360px;
    margin-bottom: 10px;
  }
  .solic__text h4 {
    font-size: 18pt;
    width: 360px;
    margin-bottom: 25px;
  }
  .cont__input {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  .asuntodiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  .asuntodiv input {
    margin-top: 10px;
    margin-left: 10px;
    width: 200px;
    flex: none;
    height: 45px;
    margin-bottom: 15px;
  }
  .container__solicitud {
    padding: 20px;
    margin-left: 0px;
    width: 320px;
  }
}
