#cont__benef {
  text-align: center;
  width: 60%;
  margin-bottom: 50px;
  padding: 30px 40px;
  position: absolute;
  top: 0px;
  left: 20%;
}

.divider_ben {
  width: 50px;
  margin: 10px auto;
}

.benitem {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.benitem h5 {
  width: 250px;
  height: 35px;
}
.benitem p {
  width: 220px;
  margin-top: 15px;
}

#bgbenef {
  width: 100%;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  margin: 0 auto;
  max-height: 500px;
  transform: scaleX(-1);
  opacity: 20%;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  #cont__benef {
    left: 20%;
    height: 460px;
  }
  .benitem {
    width: 170px;
  }
  .benitem h5 {
    width: 170px;
  }
  .benitem p {
    width: 170px;
  }
  #bgbenef {
    max-height: 550px;
  }
}
/* Media Queries (Small Devices) */

@media screen and (max-width: 600px) {
  #beneficios {
    height: 530px;
  }
  #bgbenef {
    width: 100%;
   height: 640px;
  }
  .benitem {
    width: 150px;
  }
  .benitem h5 {
    width: 120px;
  }
  .benitem p {
    width: 120px;
  }
}
