@font-face {
  font-family: "Aceh";
  src: local("Aceh-Regular"),
    url(./assets/fonts/Aceh-Regular.otf) format("opentype");
}
@font-face {
  font-family: "FuturaMd";
  src: local("Futura Md BT Medium"),
    url(./assets/fonts/Futura\ Md\ BT\ Medium.ttf) format("truetype");
}
@font-face {
  font-family: "FuturaBd";
  src: local("Futura Md BT Bold"),
    url(./assets/fonts/Futura\ Md\ BT\ Bold.ttf) format("truetype");
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #fff;
  --color-lightext: #fff;
  --color-toxicgreen: #52c306;
  --color-midblue: #406fe5;
  --color-midgreen: #006837;
  --color-textgrey: #4d4d4d;
  --color-lightgrey: #8d8d8d;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  background: var(--color-bg);
  color: var(--color-textgrey);
  line-height: 1.7;
  font-family: FuturaMd;
  width: 100%;
  height: 100%;
}

/* General Styles*/

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: FuturaBd;
}

.text-light {
  color: var(--color-lightext);
}
.btn {
  width: max-content;
  display: inline-block;
  padding: 0.3rem 1.2rem;
  border-radius: 0.8rem;
  cursor: pointer;
  transition: var(--transition);
  color: var(--color-lightext);
  background: var(--color-toxicgreen);
}
.circbtn {
  display: inline-block;
  padding: 1rem 1rem;
  border-radius: 50%;
  cursor: pointer;
  transition: var(--transition);
  color: var(--color-lightext);
  background: var(--color-toxicgreen);
}
.btn:hover {
  background: var(--color-midblue);
}
.circbtn:hover {
  background: var(--color-midblue);
}
img {
  display: block;
  object-fit: cover;
}
.titles {
  font-size: 20pt;
  color: var(--color-midgreen);
}
.cont__cont {
  position: relative;
}
