#cont_apoyo {
  text-align: center;
  width: 60%;
  margin-bottom: 50px;
}
items {
  width: 250;
}
#divider2 {
  max-height: 3px;
  max-width: 60px;
  margin-bottom: 50px;
  margin-top: 50px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.carouselcontainer {
  display: flex;
  flex-direction: row;
  margin-left: 37px;
}
.carItems {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carItems p {
  font-size: 10pt;
  width: 80%;
  text-align: center;
  margin-top: 10px;
}

.carItems img {
  height: 250px;
  max-width: 250px;
  overflow: hidden;
  margin-bottom: 20px;
}
/*___________________________ Media Queries________________________________*/

/* Media Queries (Medium Devices) */

@media screen and (max-width: 1024px) {
  #cont_apoyo {
    margin-top: 50px;
    width: 75%;
  }
  .carouselcontainer {
    margin-left: 0;
  }
  .carItems img {
    height: 200px;
    max-width: 200px;
    overflow: hidden;
    margin-bottom: 20px;
  }
}
